<template  >
  <v-card dense tile flat class="pa-2">
    <!-- <v-container class="fill-height" style="widht:100%"> -->
    <v-row no-gutters class="fill-height">
      <v-col cols="3">
        <v-card dense tile outlined class="pa-1">
          <v-text-field
            class="yellow lighten-5"
            outlined
            v-model="tableName"
            dense
            hide-details="auto"
            @input="getTableData()"
          >
          </v-text-field>
          <v-card-text class="overflow-y-auto">
             <ul :style=" 'height: calc(100%- '+ 190 + ' px) ' ">
            <li v-for="(value, name) in tableObject" :key="name">
              <div class="title" @click="showValue(name,value)">{{ name }}</div>
              <!-- <div class="cont">{{value}}</div> -->
            </li>
          </ul>
          </v-card-text>
         
        </v-card>
      </v-col>
      <v-col cols="9">
        <v-card tile dense outlined class="fill-height pa-1 ml-2">

       <div id="myChart" :style="{width: '100%', height: '100%'}"></div>

        </v-card>
      </v-col>
    </v-row>
    <!-- </v-container> -->
  </v-card>
</template>


<script>
export default {
  name: "ecu",
  data: () => ({
    tableName: "",
    tableObject: {},
    chartData:{}
  }),
  methods: {
    getTable() {
      return this.axios.post("/api/ECU/GetTabelObject", { TN: this.tableName });
    },
    getTableData() {
      this.getTable().then((rsp) => {
        var result = rsp.data;
        if (result.Success) {
          this.tableObject = result.Data;
        }
      });
    },
    showValue(name, value) {


        let myChart = this.$echarts.init(document.getElementById('myChart'))

        var x = [];
        var y = [];
        value.forEach((i) => {
            x.push(i.DateTime);
            y.push(i.TimeStamp2)
        });

        // 绘制图表
        myChart.setOption({
            title: { text: name },
            tooltip: {},
            xAxis: {
                data: x
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'bar',
                data: y
            }]
        });
        this.chartData =value;
      //alert(JSON.stringify(value));
    },
  },
};
</script>

