<template>
  <v-row no-gutters>
    <v-col cols="3" class="fill-height">
      <v-card class="pa-0 fill-height" dense tile>
        <v-toolbar color="text-h5 font-weight-bold" dense flat tile>
          {{ editOrder.OrderNr }}
        </v-toolbar>
        <v-card-text class="overflow-y-auto" style="height: calc(100% - 48px)">
          <div class="">
            <label style="color: #999"> 单据日期</label>
            <v-menu
              v-model="menuDate"
              :close-on-content-click="false"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="orderDate"
                  class="yellow lighten-5"
                  outlined
                  readonly
                  dense
                  hide-details="auto"
                  v-bind="attrs"
                  v-on="on"
                />
              </template>
              <v-date-picker
                no-title
                :month-format="fmtMonth"
                :header-date-format="fmtHeaderDate"
                :title-date-format="fmtTitleDate"
                v-model="orderDate"
                @input="menuDate = false"
              ></v-date-picker>
            </v-menu>
          </div>

          <div class="pt-5">
            <label style="color: #999"> 申请</label><br />
            <label class="subtitle-2 font-weight-bold" style="color: black">
              {{ editOrder.ToLocation }}
            </label>
          </div>

          <div class="pt-5">
            <label style="color: #999"> 出货</label><br />
            <label class="subtitle-2 font-weight-bold" style="color: black">
              {{ editOrder.FromLocation }}
            </label>
            <!-- <v-select
              :items="selectItems"
              v-model="editOrder.FromLocation"
              dense
              outlined
              hide-details="auto"
              class="yellow lighten-5"
            ></v-select> -->
          </div>

          <div class="pt-5">
            <label style="color: #999"> 备注</label>
            <v-textarea
              dense
              outlined
              class="yellow lighten-5"
              hide-details="auto"
            ></v-textarea>
          </div>
          <div class="pt-5">
            开单<br />
            <label class="subtitle-2 font-weight-bold" style="color: black">
              张大三 2021-09-29 09:25 </label
            ><br />
            审单<br />
            <label class="subtitle-2 font-weight-bold" style="color: black">
              李大伟 2021-09-29 09:25
            </label>
          </div>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="9" class="fill-height pl-3 pt-3">
      <v-container class="fill-height">
        <v-row class="fill-height pb-13">
          <v-col cols="7" class="fill-height pa-0 ma-0">
            <v-list
              dense
              style="height: 100%"
              class="pa-0 ma-0 overflow-y-auto yellow lighten-5"
            >
              <!-- <v-list-item-group style="background-color: bisque"> -->
              <template v-for="(item, index) in editOrder.List">
                <v-list-item :key="item.Code">
                  <v-list-item-content>
                    <v-list-item-title v-text="item.Code" />
                    <v-list-item-subtitle>
                      <label style="color: #00bfa5 !important">
                        {{ item.Name }}
                      </label>
                    </v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-action>
                    <v-card
                      flat
                      class="d-flex flex-row align-center yellow lighten-5"
                      tile
                      ><v-btn
                        x-small
                        icon
                        class="mr-2"
                        color="error"
                        outlined
                        @click="minusQty(item)"
                        ><v-icon dark> mdi-minus </v-icon></v-btn
                      >
                      <v-text-field
                        class="yellow lighten-5 text-h5 font-weight-bold"
                        style="width: 100px; text-align: right"
                        type="number"
                        v-model="item.Qty"
                        outlined
                        dense
                        flat
                        reverse
                        hide-details="auto"
                      ></v-text-field>
                      <v-btn
                        x-small
                        icon
                        dark
                        class="ml-2"
                        color="success"
                        outlined
                        @click="plusQty(item)"
                      >
                        <v-icon> mdi-plus</v-icon></v-btn
                      >

                      <v-btn small icon class="ml-2" @click="delList(index)">
                        <v-icon> mdi-delete </v-icon></v-btn
                      >
                    </v-card>
                  </v-list-item-action>
                </v-list-item>
                <v-divider class="mx-2" :key="item.Code + index"></v-divider>
              </template>
              <!-- </v-list-item-group>   v-if="index < order.List.length - 1" -->
            </v-list>
          </v-col>
          <v-col cols="5" class="fill-height ma-0 pa-0 px-3">
            <v-card class="fill-height" dense tile outlined>
              <v-toolbar
                color="orange"
                style="font-size: 14px"
                dark
                dense
                flat
                outlined
                tile
              >
                明细登记区
              </v-toolbar>
              <v-card-text
                class="overflow-y-auto"
                style="height: calc(100% - 48px)"
              >
                <div v-if="isSearchCode">
                  <div class="">
                    <label style="color: #999">票券编号</label>
                    <v-card flat class="d-flex flex-row" tile>
                      <v-text-field
                        dense
                        outlined
                        hide-details="auto"
                        class="yellow lighten-5"
                        v-model="equalCondition"
                        @input="equalSearch()"
                      >
                      </v-text-field>
                      <v-btn
                        class="ml-1 mt-0"
                        dark
                        color="orange"
                        rounded
                        @click="isSearchCode = false"
                      >
                        拣选
                      </v-btn></v-card
                    >
                    {{ equalItem.Name }}
                  </div>
                  <div class="pt-5">
                    <label style="color: #999">数量</label>
                    <v-card flat class="d-flex flex-row align-center" tile
                      ><v-btn
                        x-small
                        icon
                        class="mr-2"
                        color="error"
                        outlined
                        @click="equalItem.Qty--"
                        ><v-icon dark> mdi-minus </v-icon></v-btn
                      >
                      <v-text-field
                        class="yellow lighten-5 text-h5 font-weight-bold"
                        style="width: 100px; text-align: right"
                        type="number"
                        v-model="equalItem.Qty"
                        outlined
                        dense
                        flat
                        reverse
                        hide-details="auto"
                      ></v-text-field>
                      <v-btn
                        x-small
                        icon
                        dark
                        class="ml-2"
                        color="success"
                        outlined
                        @click="equalItem.Qty++"
                      >
                        <v-icon> mdi-plus</v-icon></v-btn
                      >
                    </v-card>
                  </div>

                  <div class="pt-5">
                    <v-row>
                      <v-col cols="7">
                        <v-btn
                          block
                          color="orange"
                          dark
                          rounded
                          @click="addItem()"
                          >加入清单</v-btn
                        >
                      </v-col>
                      <v-col cols="5">
                        <v-btn block color="orange" dark outlined rounded
                          >取消</v-btn
                        >
                      </v-col>
                    </v-row>
                  </div>
                </div>
                <div class="fill-height" v-if="!isSearchCode">
                  <v-text-field
                    dense
                    outlined
                    hide-details="auto"
                    class="yellow lighten-5"
                    placeholder="请输入关键字"
                    v-model="likeCondition"
                    @input="likeSearch()"
                  >
                  </v-text-field>
                  <div
                    class="py-2 ma-0 fill-height"
                    style="height: calc(100% - 78px)"
                  >
                    <v-list dense class="pa-0 ma-0 fill-height overflow-y-auto">
                      <template v-for="(item, index) in likeList">
                        <v-list-item :key="item.Code">
                          <v-list-item-content>
                            <v-list-item-title v-text="item.Code" />
                            <v-list-item-subtitle>
                              <label style="color: #00bfa5 !important">
                                {{ item.Name }}
                              </label>
                            </v-list-item-subtitle>
                          </v-list-item-content>
                          <v-list-item-action>
                            <v-card
                              flat
                              dense
                              class="d-flex flex-row align-center pa-0 ma-0"
                              tile
                              ><v-btn
                                x-small
                                icon
                                class="mr-2"
                                color="error"
                                outlined
                                @click="minusQty2(item)"
                                ><v-icon dark> mdi-minus </v-icon></v-btn
                              >

                              <v-text-field
                                class="yellow lighten-5 pa-0"
                                style="width: 100px; text-align: right"
                                type="number"
                                v-model="item.Qty"
                                outlined
                                dense
                                flat
                                reverse
                                hide-details="auto"
                              ></v-text-field>
                              <v-btn
                                x-small
                                icon
                                dark
                                class="ml-2"
                                color="success"
                                outlined
                                @click="plusQty(item)"
                              >
                                <v-icon> mdi-plus</v-icon></v-btn
                              >
                            </v-card>
                          </v-list-item-action>
                        </v-list-item>
                        <v-divider
                          class="mx-2"
                          :key="item.Code + index"
                        ></v-divider>
                      </template>
                    </v-list>
                  </div>
                  <div style="height: 50px; position: sticky">
                    <v-row>
                      <v-col cols="7">
                        <v-btn
                          block
                          color="orange"
                          dark
                          rounded
                          @click="addList()"
                          >加入清单</v-btn
                        >
                      </v-col>
                      <v-col cols="5">
                        <v-btn
                          block
                          color="orange"
                          dark
                          outlined
                          rounded
                          @click="isSearchCode = true"
                          >取消</v-btn
                        >
                      </v-col>
                    </v-row>
                  </div>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-row style="height: 60px; position: sticky; bottom: 5px">
          <v-col cols="12" class="pl-0">
            <div style="height: 40px; line-height: 40px">
              <div
                style="
                  background-color: #444;
                  color: #fff;
                  border-radius: 20px;
                  padding-left: 20px;
                  float: left;
                  width: 100%;
                  height: 40px;
                  line-height: 40px;
                "
              >
                共
                <label class="font-weight-black text-h6">
                  {{ order.List == undefined ? 0 : order.List.length }} </label
                >项, 总数<label class="font-weight-black text-h6">
                  {{ sumList() }}
                </label>

                <v-btn
                  rounded
                  dark
                  depressed
                  color="orange"
                  style="float: right; height: 100%"
                  @click="save()"
                  >保存</v-btn
                >

                <v-btn
                  rounded
                  outlined
                  color="red"
                  style="
                    float: right;
                    height: 100%;
                    background-color: white;
                    margin-right: 10px;
                  "
                  >删除</v-btn
                >
              </div>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    isSearchCode: true,
    menuDate: false,
    orderDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substr(0, 10),
    displayName: "(名称)",
    displayQty: 0,
    selectItems: [
      "广汽集团公司",
      "美的集团公司",
      "集团公司",
      "徐福记集团",
      "华为集团",
    ],
    editOrder: {
      OrderNr: "",
      FromLocation: "",
      ToLocation: "",
      List: [],
    },
    selectedItem: "",
    likeCondition: "",
    likeList: [],
    equalCondition: "",
    equalItem: {
      Code: "",
      Name: "",
      Qty: "NaN",
    },
    defItem: { Code: "", Name: "", Qty: "NaN" },
  }),
  props: ["order"],
  mounted() {
    this.editOrder = Object.assign({}, this.order);
  },
  methods: {
    save() {
      alert(" ^_^ 保存功能未完待继~~");
    },
    minusQty(item) {
      if (item.Qty > 1) item.Qty--;
    },
    minusQty2(item) {
      if (item.Qty > 1) item.Qty--;
      else item.Qty = "NaN";
    },
    plusQty(item) {
      if (item.Qty == "NaN") {
        item.Qty = 0;
      }

      item.Qty++;
    },
    likeSearch() {
      var request = { Type: "Like", Content: this.likeCondition };
      this.axios
        .post("/api/Information/GetItemByCondition", request)
        .then((response) => {
          var result = response.data;
          if (result.Success) {
            this.likeList = result.Data;
          } else {
            this.likeList = [];
          }
        });
    },
    equalSearch() {
      var request = { Type: "Equal", Content: this.equalCondition };
      this.axios
        .post("/api/Information/GetItemByCondition", request)
        .then((response) => {
          var result = response.data;
          if (result.Success) {
            if (result.Data.length == 1) {
              this.equalItem = result.Data[0];
              this.equalItem.Qty = 1;
            } else this.equalItem = Object.assign({}, this.defItem);
          } else {
            this.equalItem = Object.assign({}, this.defItem);
          }
        });
    },
    sumList() {
      var result = 0;
      this.editOrder.List.forEach((i) => {
        if (i.Qty != "NaN" && i.Qty > 0) {
          result += parseInt(i.Qty);
        }
      });
      return result;
    },
    addItem() {
      if (
        this.equalItem.Name != "" &&
        this.equalItem.Qty != "NaN" &&
        this.equalItem.Qty > 0
      ) {
        this.editOrder.List.push(Object.assign({}, this.equalItem));
        this.equalCondition = "";
        this.equalItem = Object.assign({}, this.defItem);
      }
    },
    addList() {
      this.likeList.forEach((i) => {
        if (i.Qty != "NaN" && i.Qty > 0) {
          this.editOrder.List.push(i);
        }
      });
      this.likeCondition = "";
      this.likeList = [];

      this.isSearchCode = true;
    },
    delList(index) {
      this.editOrder.List.splice(index, 1);
    },
    fmtMonth(value) {
      //alert(this.$event);
      let date = new Date(value);
      let MM = date.getMonth() + 1;
      return MM;
    },
    fmtTitleDate(value) {
      return this.dateFormat("yyyy-MM-dd", new Date(value));
    },
    fmtHeaderDate(value) {
      return this.dateFormat("yyyy-MM", new Date(value));
    },
    dateFormat(fmt, date) {
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          (date.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      }
      let o = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "h+": date.getHours(),
        "m+": date.getMinutes(),
        "s+": date.getSeconds(),
      };
      for (let k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + "";
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length === 1 ? str : ("00" + str).substr(str.length)
          );
        }
      }
      return fmt;
    },
  },
};
</script>

<style scoped>
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type="number"] {
  -moz-appearance: textfield;
}
</style>