<template>
  <v-row no-gutters>
    <v-col cols="3" class="fill-height">
      <v-card class="pa-0 fill-height" dense tile>
        <v-toolbar color="orange" style="font-size: 14px" dark dense flat tile>
          按下列任何标准进行搜索
        </v-toolbar>
        <v-card-text class="overflow-y-auto" style="height: calc(100% - 48px)">
          <div class="">
            <label style="color: #999">分支机构</label>

            <v-menu
              v-model="menuLocation"
              :close-on-content-click="false"
              min-width="auto"
              transition="slide-y-transition"
              nudge-top="-45"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  dense
                  outlined
                  readonly
                  hide-details="auto"
                  class="yellow lighten-5"
                  v-model="selected.LocationName"
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-card height="300" style="overflow: auto">
                <v-treeview
                  dense
                  tile
                  hoverable
                  activatable
                  :items="editedLocationList"
                  :active.sync="editedLocationActive"
                  @update:active="menuLocation = false"
                />
              </v-card>
            </v-menu>
          </div>

          <div class="pt-5">
            <label style="color: #999"> 单据日期</label>
            <v-menu
              v-model="menuFrom"
              :close-on-content-click="false"
              :nudge-right="38"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-row>
                  <v-col cols="1">
                    <div style="margin-top: 10px">
                      <label style="color: #999">从</label>
                    </div>
                  </v-col>
                  <v-col cols="11">
                    <v-text-field
                      v-model="dateFrom"
                      class="yellow lighten-5"
                      outlined
                      readonly
                      dense
                      hide-details="auto"
                      v-bind="attrs"
                      v-on="on"
                    >
                    </v-text-field>
                  </v-col>
                </v-row>
              </template>
              <v-date-picker
                v-model="dateFrom"
                no-title
                :month-format="fmtMonth"
                :header-date-format="fmtHeaderDate"
                :title-date-format="fmtTitleDate"
                @input="menuFrom = false"
              ></v-date-picker>
            </v-menu>
            <v-menu
              v-model="menuTo"
              :close-on-content-click="false"
              :nudge-right="38"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-row>
                  <v-col cols="1">
                    <div style="margin-top: 10px">
                      <label style="color: #999">至</label>
                    </div>
                  </v-col>
                  <v-col cols="11">
                    <v-text-field
                      v-model="dateTo"
                      class="yellow lighten-5"
                      outlined
                      readonly
                      dense
                      hide-details="auto"
                      v-bind="attrs"
                      v-on="on"
                    >
                    </v-text-field>
                  </v-col>
                </v-row>
              </template>
              <v-date-picker
                v-model="dateTo"
                no-title
                :month-format="fmtMonth"
                :header-date-format="fmtHeaderDate"
                :title-date-format="fmtTitleDate"
                @input="menuTo = false"
              ></v-date-picker>
            </v-menu>
          </div>

          <div class="pt-5">
            <label style="color: #999"> 单据编号</label>
            <v-text-field
              dense
              outlined
              hide-details="auto"
              class="yellow lighten-5"
            ></v-text-field>
          </div>

          <div class="pt-5">
            <label style="color: #999"> 状态</label>
            <v-select
              :items="selectItems"
              v-model="selectedItem"
              dense
              outlined
              hide-details="auto"
              class="yellow lighten-5"
              :menu-props="{ bottom: true, offsetY: true }"
            ></v-select>
          </div>

          <div class="pt-5">
            <v-row>
              <v-col cols="6">
                <v-btn block color="orange" dark outlined rounded>重置</v-btn>
              </v-col>
              <v-col cols="6">
                <v-btn block color="orange" dark rounded @click="btnSearch()"
                  >搜索</v-btn
                >
              </v-col>
            </v-row>
          </div>
        </v-card-text>
      </v-card>
    </v-col>

    <v-col cols="3" class="fill-height">
      <v-card class="pl-2 fill-height" dense tile>
        <v-toolbar dense flat tile style="font-size: 14px">
          共 {{ orderItems.length }} 项
        </v-toolbar>
        <v-divider color="orange" />
        <v-card-text
          class="overflow-y-auto pa-0 ma-0"
          style="height: calc(100% - 48px)"
        >
          <v-list dense class="pa-0 ma-0">
            <v-list-item-group v-model="orderSelectedItem" color="primary">
              <v-list-item
                v-for="item in orderItems"
                :key="item.OrderNr"
                @click="activeItem(item)"
                style="width: 100%; margin-bottom: 5px; margin-top: 5px"
              >
                <v-list-item-content>
                  <v-row>
                    <v-col cols="1">
                      <div
                        v-if="item.Status == '1'"
                        style="
                          background-color: forestgreen;
                          width: 5px;
                          height: 100%;
                        "
                      ></div>
                      <div
                        v-else
                        style="
                          background-color: orange;
                          width: 5px;
                          height: 100%;
                        "
                      ></div>
                    </v-col>
                    <v-col cols="10">
                      <v-list-item-title class="subtitle-1 font-weight-bold">
                        {{ item.OrderNr }}
                      </v-list-item-title>
                      <v-list-item-subtitle>
                        {{ item.FromLocation
                        }}<v-icon dense>mdi-arrow-right</v-icon>
                        {{ item.ToLocation }}
                      </v-list-item-subtitle>
                    </v-col>
                  </v-row>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="6" class="fill-height">
      <v-card class="pl-2 fill-height" dense tile>
        <v-toolbar dense flat tile>
          <v-row>
            <v-col cols="9">
              <label class="text-h5 font-weight-bold">
                {{ orderActiveItem.OrderNr }}
              </label>
            </v-col>
            <v-col cols="3" style="text-align: right; margin-top: 10px">
              <div class="caption font-italic">2021-09-29</div>
            </v-col>
          </v-row>
        </v-toolbar>
        <v-divider color="orange" />
        <v-card-text style="height: calc(100% - 48px)">
          <v-row>
            <v-col cols="4">
              <label style="color: #999">申请</label><br />
              <label class="subtitle-2 font-weight-bold" style="color: black">
                {{ orderActiveItem.ToLocation }} </label
              ><br />
              <label style="color: #999"> 出货</label><br />
              <label class="subtitle-2 font-weight-bold" style="color: black">
                {{ orderActiveItem.FromLocation }}
              </label>
            </v-col>
            <v-col cols="3">
              <v-img
                v-if="orderActiveItem.Status == '1'"
                :src="require('../../assets/img/sender.png')"
                style="width: 100px"
              ></v-img>
            </v-col>
            <v-col cols="5">
              开单<br />
              <label class="subtitle-2 font-weight-bold" style="color: black">
                张大三 2021-09-29 09:25 </label
              ><br />
              审单<br />
              <label class="subtitle-2 font-weight-bold" style="color: black">
                李大伟 2021-09-29 09:25
              </label>
            </v-col>
          </v-row>
          <v-row v-if="orderActiveItem.Remark" style="height: 50px">
            <v-col cols="12">
              <v-alert border="left" dense outlined color="green" class="ma-0">
                {{ orderActiveItem.Remark }}
              </v-alert>
            </v-col>
          </v-row>
          <v-row style="height: 100%">
            <v-col cols="12">
              <v-list
                dense
                class="pa-0 ma-0 yellow lighten-5 overflow-y-auto"
                :style="
                  'min-height: calc(100% - ' +
                  (orderActiveItem.Remark ? 195 : 144) +
                  'px); height: calc(100% - ' +
                  (orderActiveItem.Remark ? 195 : 144) +
                  'px); max-height: calc(100% - ' +
                  (orderActiveItem.Remark ? 195 : 144) +
                  'px); '
                "
              >
                <template v-for="(item, index) in orderActiveItem.List">
                  <v-list-item :key="item.Code">
                    <v-list-item-content>
                      <v-list-item-title v-text="item.Code" />
                      <v-list-item-subtitle>
                        <label style="color: #00bfa5 !important">
                          {{ item.Name }}
                        </label>
                      </v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-action>
                      <label class="font-weight-black text-h5">
                        {{ item.Qty }}
                      </label>
                    </v-list-item-action>
                  </v-list-item>
                  <v-divider class="mx-2" :key="item.Code + index" />
                </template>
                <!-- v-if="index < orderActiveItem.List.length - 1" -->
              </v-list>
            </v-col>
          </v-row>
          <v-row style="height: 60px; position: sticky; bottom: 5px">
            <v-col cols="12">
              <div style="height: 40px; line-height: 40px; width: 100%">
                <div
                  style="
                    background-color: #444;
                    color: #fff;
                    border-radius: 20px;
                    padding-left: 20px;
                    float: left;
                    width: calc(100% - 85px);
                    height: 40px;
                    line-height: 40px;
                  "
                >
                  共
                  <label class="font-weight-black text-h6">
                    {{
                      orderActiveItem.List == undefined
                        ? 0
                        : orderActiveItem.List.length
                    }} </label
                  >项, 总数<label class="font-weight-black text-h6">
                    {{ orderActiveTotal }}
                  </label>

                  <v-btn
                    rounded
                    outlined
                    color="orange"
                    style="
                      float: right;
                      height: 100%;
                      margin-left: 10px;
                      background-color: white;
                    "
                    >复制</v-btn
                  >

                  <v-btn
                    rounded
                    dark
                    depressed
                    color="orange"
                    style="float: right; height: 100%"
                    @click="editOrder()"
                    >修改</v-btn
                  >
                </div>

                <v-btn
                  rounded
                  dark
                  depressed
                  color="orange"
                  style="float: right; height: 100%"
                  @click="createOrder()"
                  >新增</v-btn
                >
              </div>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
//var linq = require("linq");
export default {
  name: "search",
  data: () => ({
    active: [],
    fullHeight: document.documentElement.clientHeight,
    menuFrom: false,
    menuTo: false,
    menuLocation: false,
    dateTo: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substr(0, 10),
    dateFrom: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substr(0, 10),
    selectItems: ["全部", "未发货", "已发货"],
    selectedItem: "",
    editedLocationList: [],
    editedLocationItem: { LocationName: "" },
    editedLocationID: "",
    editedLocationActive: [],
    locationData: [],
    orderItems: [
      {
        OrderNr: "MR21070600001",
        FromLocation: "集团公司",
        ToLocation: "汇桥店",
        Status: "1",
        Remark: "这里是备注内容，敬请留意。",
        List: [
          { Code: "1", Name: "五仁月饼劵", Qty: 30 },
          { Code: "2", Name: "双黄白莲月饼劵", Qty: 30 },
        ],
      },
      {
        OrderNr: "MR21070600002",
        FromLocation: "集团公司",
        ToLocation: "汇桥店",
        Status: "1",
        List: [
          { Code: "1", Name: "五仁月饼劵", Qty: 12 },
          { Code: "2", Name: "双黄白莲月饼劵", Qty: 25 },
          { Code: "3", Name: "蛋黄白莲月饼劵", Qty: 25 },
          { Code: "4", Name: "纯流心奶黄月饼劵", Qty: 25 },
          { Code: "5", Name: "流星月礼盒劵", Qty: 25 },
          { Code: "6", Name: "冰皮流心套装劵", Qty: 25 },
          { Code: "7", Name: "纯流心奶黄月饼劵", Qty: 25 },
          { Code: "8", Name: "流星月礼盒劵", Qty: 25 },
          { Code: "9", Name: "冰皮流心套装劵", Qty: 25 },
        ],
      },
      {
        OrderNr: "MR21070600003",
        FromLocation: "集团公司",
        ToLocation: "汇桥店",
        Status: "0",
        List: [
          { Code: "1", Name: "五仁月饼劵", Qty: 12 },
          { Code: "2", Name: "双黄白莲月饼劵", Qty: 25 },
          { Code: "3", Name: "蛋黄白莲月饼劵", Qty: 25 },
          { Code: "4", Name: "纯流心奶黄月饼劵", Qty: 25 },
        ],
      },
      {
        OrderNr: "MR21070600004",
        FromLocation: "集团公司",
        ToLocation: "汇桥店",
        Status: "1",
        List: [
          { Code: "1", Name: "五仁月饼劵", Qty: 12 },
          { Code: "2", Name: "双黄白莲月饼劵", Qty: 25 },
          { Code: "3", Name: "蛋黄白莲月饼劵", Qty: 25 },
          { Code: "4", Name: "纯流心奶黄月饼劵", Qty: 25 },
          { Code: "6", Name: "冰皮流心套装劵", Qty: 25 },
        ],
      },
      {
        OrderNr: "MR21070600005",
        FromLocation: "集团公司",
        ToLocation: "汇桥店",
        Status: "1",
        List: [
          { Code: "3", Name: "蛋黄白莲月饼劵", Qty: 25 },
          { Code: "4", Name: "纯流心奶黄月饼劵", Qty: 25 },
          { Code: "5", Name: "流星月礼盒劵", Qty: 25 },
          { Code: "6", Name: "冰皮流心套装劵", Qty: 25 },
        ],
      },
      {
        OrderNr: "MR21070600006",
        FromLocation: "集团公司",
        ToLocation: "汇桥店",
        Status: "0",
        List: [
          { Code: "1", Name: "五仁月饼劵", Qty: 12 },
          { Code: "2", Name: "双黄白莲月饼劵", Qty: 25 },
          { Code: "3", Name: "蛋黄白莲月饼劵", Qty: 25 },
          { Code: "6", Name: "冰皮流心套装劵", Qty: 25 },
        ],
      },
    ],
    orderSelectedItem: null,
    orderActiveItem: { List: [] },
    orderActiveTotal: 0,
  }),

  created() {
    this.activeItem(this.orderItems[0]);
    this.orderSelectedItem = 0;
    this.initView();
  },
  methods: {
    btnSearch() {
      alert(this.active);
    },
    updateTreeItem() {
      //  var tmp = linq.from(this.editedLocationList).firstOrDefault((i) => i.ID == this.editedLocationItem, null);
      //             if (tmp != null) {
      //                 this.editedLocationName = tmp.Name;
      //                 alert(tmp.Name);
      //             }

      this.editedLocationList.forEach((i) => {
        if (i.ID == this.editedLocationItem) {
          this.editedLocationName = i.Name;
        } else {
          alert(i.ID);
          alert(this.editedLocationItem);
        }
      });
    },
    getLocationList() {
      return this.axios.post("/api/Security/GetLocationForALL");
    },
    listToTree(list, parentId) {
      let menuObj = {};
      list.forEach((item) => {
        item.id = item.ID;
        item.name = item.LocationCode + "-" + item.LocationName;
        item.open = true;
        item.children = [];
        menuObj[item.id] = item;
      });
      return list.filter((item) => {
        if (item.ParentID !== parentId) {
          menuObj[item.ParentID].children.push(item);
          return false;
        }
        return true;
      });
    },
    initView() {
      let that = this;
      this.getLocationList().then((rsp) => {
        let result = rsp.data;
        if (result.Success) {
          that.locationData = result.Data;
          that.editedLocationList = that.listToTree(result.Data, null);
        }
      });
    },

    activeItem(item) {
      let that = this;
      that.orderActiveItem = item;
      that.orderActiveTotal = 0;
      if (item.List != undefined) {
        item.List.forEach((el) => {
          that.orderActiveTotal += parseInt(el.Qty);
        });
      }
    },
    createOrder() {
      this.$emit("orderTab", { OrderNr: "新的" });
    },
    editOrder() {
      this.$emit("orderTab", Object.assign({}, this.orderActiveItem));
    },
    fmtMonth(value) {
      //alert(this.$event);
      let date = new Date(value);
      let MM = date.getMonth() + 1;
      return MM;
    },
    fmtTitleDate(value) {
      return this.dateFormat("yyyy-MM-dd", new Date(value));
    },
    fmtHeaderDate(value) {
      return this.dateFormat("yyyy-MM", new Date(value));
    },
    dateFormat(fmt, date) {
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          (date.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      }
      let o = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "h+": date.getHours(),
        "m+": date.getMinutes(),
        "s+": date.getSeconds(),
      };
      for (let k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + "";
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length === 1 ? str : ("00" + str).substr(str.length)
          );
        }
      }
      return fmt;
    },
  },
  computed: {
    selected() {
      if (!this.editedLocationActive.length) return { LocationName: "" };

      const id = this.editedLocationActive[0];

      return this.locationData.find((user) => user.ID === id);
    },
  },
};
</script>

<style scoped>
.v-treeview-node {
  background: white;
}
</style>