<template>
    <v-row no-gutters style="flex-wrap: nowrap;">
        <v-col cols="3" class="flex-grow-0 flex-shrink-0">
            <v-card tile style="height:100%">
                <v-toolbar color="info" dense flat tile dark>
                    <v-toolbar-title>角色列表 ({{roleList.length}})</v-toolbar-title>
                    <v-spacer />
                    <v-btn icon>
                        <v-icon>mdi-plus</v-icon>
                    </v-btn>
                </v-toolbar>
                <v-list dense>
                    <v-list-item-group v-model="roelSelectItem" color="primary">
                        <v-list-item v-for="item in roleList" :key="item.ID">
                            <v-list-item-content>
                                <v-list-item-title>{{item.RoleName}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list-item-group>
                </v-list>
            </v-card>
        </v-col>
        <v-divider vertical></v-divider>
        <v-col cols="10" style="min-width: 100px; " class="flex-grow-0 flex-shrink-1">
            <v-card class="pa-2" dense tile style="height:100%">
                I'm 5 column wide and I shrink if there's not enough space
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
    export default {
        name: 'role',
        data() {
            return {
                roleList: new Array(),
                roelSelectItem: null
            }
        },
        created() {
            this.getRoleList();
        },
        methods: {
            getRoleList() {
                this.axios.post('/api/Security/GetRoleForALL')
                    .then((response) => {
                        var result = response.data;
                        if (result.Success) {
                            this.roleList = result.Data;
                        }
                    })
            }
        }
    }
</script>

