<template>
    <v-row no-gutters style="flex-wrap: nowrap">
        <v-col cols="3" class="flex-grow-0 flex-shrink-0">
            <v-card dense tile class="fill-height">
                <v-toolbar color="info" dense flat tile dark>
                    用户列表 ({{ userList.length }})
                    <v-spacer />
                    <v-btn icon small>
                        <v-icon small @click="createItem()">mdi-plus</v-icon>
                    </v-btn>
                </v-toolbar>
                <v-list dense
                        class="pa-0 ma-0 overflow-y-auto"
                        style="height: calc(100% - 45px)">
                    <v-list-item-group v-model="userSelectItem" color="primary">
                        <v-list-item v-for="item in userList"
                                     :key="item.ID"
                                     @click="activeItem(item)">
                            <v-list-item-content>
                                <v-list-item-title v-text="item.LoginAccount" />
                                <v-list-item-subtitle v-text="item.UserName" />
                            </v-list-item-content>
                            <v-list-item-action>
                                <v-btn icon @click="editItem(item)">
                                    <v-icon small>mdi-pencil</v-icon>
                                </v-btn>
                            </v-list-item-action>
                        </v-list-item>
                    </v-list-item-group>
                </v-list>
            </v-card>
        </v-col>
        <v-divider vertical />
        <v-col cols="10" class="flex-grow-0 flex-shrink-1">
            <v-list class="overflow-y-auto fill-height">
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title v-text="userSelected.LoginAccount" />
                        <v-list-item-subtitle v-text="userSelected.UserName"></v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-action>
                        <v-list-item-action-text class="primary--text">更新时间</v-list-item-action-text>{{ formateDate(userSelected.LastUpdateDatetime) }}
                    </v-list-item-action>
                </v-list-item>
                <v-divider class="mx-4" />
                <v-subheader class="primary--text" dense>角色</v-subheader>
                <v-list-item>
                    <v-item-group>
                        <v-item v-for="n in userSelected.RoleList" :key="n.ID">
                            <v-chip class="ma-2">{{ n.RoleName }}</v-chip>
                        </v-item>
                    </v-item-group>
                </v-list-item>
                <v-divider class="mx-4" />
                <v-subheader class="primary--text" dense>组织</v-subheader>
                <v-list-item>
                    <v-treeview activatable
                                dense
                                class="text-body-2"
                                style="width: 100%"
                                :items="locationTree"
                                :open.sync="open"></v-treeview>
                </v-list-item>
            </v-list>
            <v-dialog v-model="dialog" max-width="640px" scrollable persistent>
                <v-card dense tile>
                    <v-toolbar dense dark tile color="primary">
                        {{ formTitle }} <v-spacer /><v-btn small icon @click="close"><v-icon small>mdi-close</v-icon></v-btn>
                    </v-toolbar>
                    <v-card-text>
                        <v-container>
                            <v-row dense class="mt-1">
                                <v-col cols="12">
                                    <v-text-field dense
                                                  v-model="editedItem.LoginAccount"
                                                  label="账号"
                                                  :readonly="editedItem.ID !== null" />
                                </v-col>
                                <v-col cols="12">
                                    <v-text-field dense
                                                  v-model="editedItem.UserName"
                                                  label="名称" />
                                </v-col>
                                <v-col cols="12">
                                    <label style="font-size: inherit" dense>角色</label>
                                    <!-- <v-card dense tile> -->
                                    <v-chip-group multiple
                                                  active-class="primary--text"
                                                  v-model="editedItem.Roles">
                                        <v-chip v-for="role in roleList"
                                                :key="role.ID"
                                                :value="role.ID">
                                            {{ role.RoleName }}
                                        </v-chip>
                                    </v-chip-group>
                                    <!-- </v-card> -->
                                </v-col>
                                <v-col cols="12">
                                    <label style="font-size: inherit" dense>组织</label>
                                    <!-- activatable 显示选中的蓝色 -->
                                    <!-- selectable 显示勾选ChkBox -->
                                    <!-- v-model 绑定默认选中的ID 数组 ['ID1','ID2'] -->
                                    <v-card outlined tile>
                                        <v-treeview style="height: 200px"
                                                    class="overflow-y-auto fill-height"
                                                    dense
                                                    tile
                                                    activatable
                                                    selectable
                                                    :items="editedLocationItems"
                                                    v-model="editedItem.Locations" />
                                    </v-card>
                                </v-col>
                                <v-col cols="12">
                                    <label style="font-size: inherit" dense>有效性</label>
                                    <v-switch v-model="editedItem.Effectiveness"
                                              inset dense
                                              :label="`${editedItem.Effectiveness?`生效`:`失效`}`"
                                              :color="editedItem.Effectiveness?'green':'red'">
                                    </v-switch>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-card-text>
                    <v-divider />
                    <v-card-actions>
                        <v-btn color="blue darken-1" left text @click="test">密码重置</v-btn>
                        <v-spacer />

                        <v-btn color="blue darken-1" text @click="save"><v-icon left>mdi-content-save</v-icon>保存</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
            <v-snackbar v-model="snackbar" :timeout="snackbartimeout" color="primary">
                {{ snackbartext }}
                <template v-slot:action="{ attrs }">
                    <v-btn icon color="blue" v-bind="attrs" @click="snackbar = false">
                        <v-icon small>mdi-close</v-icon>
                    </v-btn>
                </template>
            </v-snackbar>
        </v-col>
    </v-row>
</template>
<script>
    import { formateDate } from "../../plugins/common";
    var linq = require("linq");
    export default {
        name: "user",
        data() {
            return {
                open: [],
                userList: [],
                roleList: [],
                locationTree: [],
                userSelectItem: null,
                userSelected: new Object(),
                editedIndex: -1,
                editedItem: {
                    LoginAccount: "",
                    UserName: "",
                },
                editedLocationItems: [],
                defaultItem: {
                    ID: null,
                    LoginAccount: "",
                    UserName: "",
                    Effectiveness: true,
                },
                dialog: false,
                snackbar: false,
                snackbartext: "",
                snackbartimeout: 2000,
            };
        },
        created() {
            this.initView();
        },
        computed: {
            formTitle() {
                return this.editedIndex === -1 ? "新增用户" : "修改用户";
            },
        },
        methods: {
            test() {
                //this.snackbartext;
                alert(this.snackbartext);
            },
            getUserList() {
                return this.axios.post("/api/Security/GetUserForALL");
            },
            getRoleList() {
                return this.axios.post("/api/Security/GetRoleForALL");
            },
            getLocationList() {
                return this.axios.post("/api/Security/GetLocationForALL");
            },
            getUserByID(item) {
                return this.axios.post("/api/Security/GetUserByID", item);
            },
            saveUser(item) {
                return this.axios.post("/api/Security/SaveUser", item);
            },
            initView() {
                let that = this;
                this.getUserList().then((response) => {
                    let result = response.data;
                    if (result.Success) {
                        that.userList = result.Data;
                        if (that.userList != null && that.userList.length > 0) {
                            that.userSelectItem = 0;
                            that.activeItem(that.userList[0]);
                        }
                    }
                });
            },
            activeItem(item) {
                let that = this;
                this.open = new Array();
                this.axios.all([this.getUserByID(item), this.getLocationList()]).then(
                    this.axios.spread(function (u, l) {
                        var showLocations = [];
                        let locations = [];

                        let lResult = l.data;
                        if (lResult.Success) {
                            locations = lResult.Data;
                        }

                        let uResult = u.data;
                        if (uResult.Success) {
                            that.userSelected = uResult.Data;
                            var userLocation = uResult.Data.LocationList;

                            userLocation.forEach((x) => {
                                var srcItem = linq
                                    .from(locations)
                                    .firstOrDefault((y) => y.ID == x.LocationID, null);
                                showLocations.push(srcItem);
                                that.getLocationParent(
                                    srcItem.ParentID,
                                    locations,
                                    showLocations
                                );
                            });
                        }

                        that.locationTree = that.listToTree(showLocations, null);
                        locations.forEach((i) => {
                            that.open.push(i.ID);
                        });
                    })
                );
            },
            getLocationParent(parentId, src, list) {
                var parentList = linq
                    .from(src)
                    .where((i) => i.ID == parentId)
                    .toArray();
                parentList.forEach((item) => {
                    var tmp = linq.from(list).firstOrDefault((i) => i.ID == item.ID, null);
                    if (tmp == null) {
                        list.push(item);
                    }

                    if (item.ParentID != null)
                        this.getLocationParent(item.ParentID, src, list);
                });
            },
            formateDate(time) {
                if (time == null) return "";

                let date = new Date(time);
                return formateDate(date, "YYYY-MM-dd hh:mm");
            },
            createItem() {
                this.editedIndex = -1;
                this.editedItem = Object.assign({}, this.defaultItem);
                this.dialog = true;
            },
            editItem(item) {
                this.editedIndex = this.userList.indexOf(item);
                let that = this;
                this.axios
                    .all([
                        this.getRoleList(),
                        this.getUserByID(item),
                        this.getLocationList(),
                    ])
                    .then(
                        this.axios.spread(function (r, u, l) {
                            let result = r.data;
                            if (result.Success) {
                                that.roleList = result.Data;
                            }

                            let lResult = l.data;
                            if (lResult.Success) {
                                that.editedLocationItems = that.listToTree(lResult.Data, null);
                            }

                            let uResult = u.data;
                            if (uResult.Success) {
                                that.editedItem = uResult.Data;
                                let locations = [];
                                if (uResult.Data.LocationList != null) {
                                    uResult.Data.LocationList.forEach((i) => {
                                        locations.push(i.LocationID);
                                    });
                                }
                                that.editedItem.Locations = locations;

                                let roles = [];
                                if (uResult.Data.RoleList != null) {
                                    uResult.Data.RoleList.forEach((y) => {
                                        roles.push(y.RoleID);
                                    });
                                }
                                that.editedItem.Roles = roles;
                            }
                        })
                    );
                this.dialog = true;
            },
            close() {
                this.dialog = false;
                this.$nextTick(() => {
                    this.editedItem = Object.assign({}, this.defaultItem);
                    this.editedIndex = -1;
                });
            },
            save() {
                let postItem = Object.assign({}, this.editedItem);
                postItem.CreatorID = "60B6C4AF-6702-4B35-878B-FF63FDB0657F";
                var isNew = this.editedIndex === -1;
                if (isNew) {
                    postItem.ID = null;
                }
                postItem.UserRoleTable = [];
                postItem.Roles.forEach((i) => {
                    postItem.UserRoleTable.push({ ID: i });
                });

                postItem.UserLocationTable = [];
                postItem.Locations.forEach((i) => {
                    postItem.UserLocationTable.push({ ID: i });
                });

                let that = this;
                this.saveUser(postItem).then((response) => {
                    let result = response.data;
                    if (result.Success) {
                        that.snackbartext = "保存成功。";
                        that.snackbar = true;

                        that.initView();

                        //that.activeItem(this.editedItem)
                        that.close();
                    } else {
                        that.snackbartext = result.Message;
                        that.snackbar = true;
                    }
                });
            },
            listToTree(list, parentId) {
                let menuObj = {};
                list.forEach((item) => {
                    item.id = item.ID;
                    item.name = item.LocationCode + "-" + item.LocationName;
                    item.open = true;
                    item.children = [];
                    menuObj[item.id] = item;
                });
                return list.filter((item) => {
                    if (item.ParentID !== parentId) {
                        menuObj[item.ParentID].children.push(item);
                        return false;
                    }
                    return true;
                });
            },
        },
    };
</script>

<style scoped>
</style>