      </v-list>
    </v-navigation-drawer>
    <v-main class="d-flex" style="height: inherit">
      <v-container
        class="d-flex"
        style="width: 100%; max-width: 100%; height: 100%"
      >
        <v-card flat outlined tile class="mx-auto" style="width: 100%">
          <v-tabs v-model="currentItem">
            <v-tab v-for="item in items" :key="item.key">
              {{ item.tab }}
              <v-icon right small @click="closeTab(item)">mdi-close</v-icon>
            </v-tab>
          </v-tabs>
          <v-tabs-items v-model="currentItem">
            <v-tab-item v-for="item in items" :key="item.key">
              <v-divider class="mx-auto"></v-divider>

              <component
                v-bind:is="item.content"
                v-if="item.tab === '查询'"
                @orderTab="orderTab"
                :style="'height:' + (fullHeight - 75) + 'px'"
              ></component>

              <component
                v-bind:is="item.content"
                v-else-if="item.tab === '新增' || item.tab === '修改'"
                :order="item.order"
                :style="'height:' + (fullHeight - 75) + 'px'"
              ></component>

              <component
                v-bind:is="item.content"
                v-else
                :style="'height:' + (fullHeight - 75) + 'px'"
              ></component>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-container>
    </v-main>
  </div>
</template>
<script>
import userMTR from "./security/UserMTR.vue";
import roleMTR from "./security/RoleMTR.vue";
import orderSearch from "./order/Search.vue";
import orderEdit from "./order/Edit.vue";
import Ecu from "./ecu/TestChart.vue";

export default {
  name: "home",
  data() {
    return {
      fullHeight: document.documentElement.clientHeight,
      mini: true,
      currentItem: null,
      items: [],
      tmpOrder: null,
    };
  },
  created() {
    this.fetchData();
  },
  mounted() {
    const that = this;

    window.onresize = function () {
      setTimeout(function () {
        that.refresh();
      }, 400);
    };
  },
  methods: {
    refresh() {
      this.fullHeight = document.documentElement.clientHeight;
    },
    fetchData() {
      if (this.fullHeight == null) {
        this.fullHeight = document.documentElement.clientHeight;
      }
    },
    newTab() {
      this.orderTab({ OrderNr: "新的" });
    },
    orderTab(obj) {
      var pass = true;
      var item;
      for (var i = 0; i < this.items.length; i++) {
        if (this.items[i].key == obj.OrderNr) {
          item = i;
          pass = false;
          break;
        }
      }
      if (pass) {
        if (obj.OrderNr == "新的") {
          this.items.push({
            tab: "新增",
            key: "新的",
            content: Object.assign({}, orderEdit),
            order: Object.assign(
              { ToLocation: "汇侨店", FromLocation: "集团公司", List: [] },
              obj
            ),
          });
        } else {
          this.items.push({
            tab: "修改",
            key: obj.OrderNr,
            content: Object.assign({}, orderEdit),
            order: Object.assign({}, obj),
          });
        }
        item = this.items.length - 1;
      }

      this.$nextTick(() => {
        this.currentItem = item;
      });
    },
    activeTab(str) {
      var pass = true;
      var item;
      for (var i = 0; i < this.items.length; i++) {
        if (this.items[i].tab == str) {
          item = i;
          pass = false;
          break;
        }
      }

      if (pass) {
        switch (str) {
          case "用户":
            this.items.push({ tab: str, key: str, content: userMTR });
            break;
          case "角色":
            this.items.push({ tab: str, key: str, content: roleMTR });
            break;
          case "查询":
            this.items.push({ tab: str, key: str, content: orderSearch });
            break;
          case "图表":
            this.items.push({ tab: str, key: str, content: Ecu });
            break;
          //case "新增":
          //    this.items.push({
          //        tab: str, content: Object.assign({}, orderEdit)
          //    });
          //    break;
        }
        item = this.items.length - 1; //this.items[this.items.length - 1];
      }
      this.$nextTick(() => {
        this.currentItem = item;
      });
    },
    closeTab(item) {
      this.items.splice(this.items.indexOf(item), 1);
      if (this.items.length == 0) {
        this.$nextTick(() => {
          this.currentItem = 0;
        });
      }
    },
  },
};
</script>
